import pageHeader from '../../../components/pageheader/pageHeader.vue';
import filterView from '../../../components/filterView/filterView.vue';
import shopLists from '../../../components/shopLists/shopLists.vue';
import interfaces from '../../../utils/interfaces.js';

export default {
	data() {
		return {
			swipeImgs: [],
			entries: [],
			shopList: [],
			filterData: {},
			indicatorDots: true,
			autoplay: true,
			interval: 2000,
			duration: 500,
			circular: true,
			page: 1,
			size: 5,
			allLoaded: false,
			postData: null,
			upDate: false,
			scrollY: 0,
			filterTop: null
		};
	},
	onLoad() {
		this.getBanner();
		this.shopLists();
		this.getFilter();
	},
	components: {
		pageHeader,
		filterView,
		shopLists
	},
	// onReachBottom() {
	// 	if (!this.allLoaded) {
	// 		this.page++;
	// 		this.upDate = false;
	// 		this.shopLists();
	// 	}
	// },
	watch: {
		scrollY(newY) {
			console.log(newY);
			console.log(this.filterTop);
			if (newY >= this.filterTop) {
				this.$refs.filterViews.fatherScrollShow();
			} else {
				this.$refs.filterViews.fatherScrollHide();
			}
		}
	},
	mounted() {
		this.$nextTick(function() {
			uni
				.createSelectorQuery()
				.in(this)
				.select('#filterSection')
				.boundingClientRect(data => {
					console.log(data);
					this.filterTop = data.top;
				})
				.exec();
		});
	},
	methods: {
		scrolltolower() {
			if (!this.allLoaded) {
				this.page++;
				this.upDate = false;
				this.shopLists();
			}
		},
		getBanner() {
			this.request({
				url: interfaces.getBanner,
				success: res => {
					// console.log(res);
					this.swipeImgs = res.swipeImgs;
					this.entries = res.entries;
				}
			});
		},
		getFilter() {
			this.request({
				url: interfaces.getfilter,
				success: res => {
					// console.log(res);
					this.filterData = res;
				}
			});
		},
		shopLists() {
			this.allLoaded = false;

			this.request({
				url: interfaces.getShoplists + '/' + this.page + '/' + this.size,
				// url: interfaces.getShoplists,
				data: this.postData,
				method: 'POST',
				success: res => {
					console.log(res);
					// this.shopList = res;
					if (!this.upDate) {
						if (res.length >= this.size) {
							res.forEach(item => {
								this.shopList.push(item);
							});
						} else {
							this.allLoaded = true;
						}
					} else {
						this.shopList = res;
					}
				}
			});
		},
		update(condation) {
			// console.log(condation);
			this.postData = condation;
			this.page = 1;
			this.upDate = true;
			this.shopLists();
		},
		scroll(e) {
			// console.log(e.detail.scrollTop);
			this.scrollY = e.detail.scrollTop;
			// console.log(this.scrollY)
		}
	}
};
